@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Montserrat", sans-serif;
}

body {
  margin: 0;
  background-color: #111;
  font-weight: 300;
}

.Movie_movie___zQdB {
  margin: 1rem;
  padding: 1rem;
  background-color: #230052;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 12px;
  text-align: center;
  color: white;
}

.Movie_movie___zQdB h2 {
  font-size: 2rem;
  color: #f7e702;
}

.Movie_movie___zQdB h3 {
  color: #eccf77;
  margin: 0;
  font-size: 1rem;
}

.Movie_movie___zQdB p {
  line-height: 1.5;
}

.MoviesList_movies-list__1niO2 {
  list-style: none;
  margin: 0;
  padding: 0;
}

section {
  margin: 1rem auto;
  width: 90%;
  max-width: 40rem;
  text-align: center;
  background-color: white;
  padding: 2rem;
  border-radius: 12px;
}

button {
  font: inherit;
  cursor: pointer;
  background: #230052;
  border: 1px solid #230052;
  color: white;
  padding: 0.75rem 2rem;
  border-radius: 20px;
}

button:focus {
  outline: none;
}

button:hover,
button:active {
  background-color: #460897;
  border-color: #460897;
}

.AddMovie_control__2O1UH {
  margin: 1rem 0;
}

.AddMovie_control__2O1UH label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
  text-align: left;
}

.AddMovie_control__2O1UH input,
.AddMovie_control__2O1UH textarea {
  display: block;
  width: 100%;
  font: inherit;
  padding: 0.2rem;
  border-radius: 12px;
  border: 1px solid #ccc;
}

.AddMovie_control__2O1UH input:focus,
.AddMovie_control__2O1UH textarea:focus {
  outline: none;
  border-color: #230052;
}
